async function carregarLojas() {
  loading.value = true;
  try {
    const token = localStorage.getItem('token');
    if (token === 'homolog-token') {
      lojas.value = [];
      loading.value = false;
      return;
    }
    const user = getCurrentUser();
    let url = `https://gerenciamento-lojas-calculadora-precos.onrender.com/lojas`;
    // Se não for admin, filtra por funcionário
    if (user && user.id && user.id !== 0) {
      url += `?funcionario_id=${user.id}`;
    }
    const resp = await authFetch(url);
    let data = await resp.json();
    // Se não for admin, filtra no frontend também
    if (user && user.id && user.id !== 0) {
      data = data.filter((l: Loja) => l.funcionario_id === user.id);
    }
    lojas.value = data;
    lojas.value.sort((a, b) => (a.cliente_nome ?? '').localeCompare(b.cliente_nome ?? ''));
  } finally {
    loading.value = false;
  }
}

async function atualizarLoja(loja: Loja) {
  const token = localStorage.getItem('token');
  if (token === 'homolog-token') return;
  await authFetch(`https://gerenciamento-lojas-calculadora-precos.onrender.com/lojas/${loja.id}`, {
    method: "PUT",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(loja)
  });
}

function exportarRelatorioExcel() {
  try {
    const workbook = new ExcelJS.Workbook();
    const worksheet = workbook.addWorksheet("Relatório");

    // Cabeçalho com quebra de linha
    const header = [
      "Nome do cliente",
      "Nome da loja",
      "Quantos anuncios\nno total na loja",
      "Quantos anuncios\nfeitos na semana",
      "Quantos anuncios\notimizados na semana",
      "Quantas visitas\nesta tendo na loja na semana",
      "Qual produto é\nmais visitado no total",
      "Quantas vendas\naté hoje no total"
    ];

    worksheet.addRow(header);

    // Adiciona os dados das lojas
    lojas.value.forEach(loja => {
      worksheet.addRow([
        loja.cliente_nome,
        loja.nome,
        loja.anuncios_total,
        loja.anuncios_realizados,
        loja.anuncios_otimizados,
        loja.visitas_semana,
        loja.produto_mais_visitado,
        loja.vendas_total
      ]);
    });

    // Formatação do cabeçalho
    worksheet.getRow(1).height = 40;
    worksheet.columns = [
      { width: 20 },
      { width: 20 },
      { width: 25 },
      { width: 28 },
      { width: 30 },
      { width: 35 },
      { width: 35 },
      { width: 28 }
    ];

    worksheet.getRow(1).eachCell(cell => {
      cell.alignment = { vertical: "middle", horizontal: "center", wrapText: true };
      cell.fill = {
        type: "pattern",
        pattern: "solid",
        fgColor: { argb: "FFF4B084" } // laranja claro
      };
      cell.font = { bold: true };
    });

    // Gera o arquivo e faz download
    workbook.xlsx.writeBuffer().then(buffer => {
      const blob = new Blob([buffer], {
        type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      });
      const nome = `relatorio-${new Date().toISOString().slice(0, 10)}.xlsx`;
      saveAs(blob, nome);
    });
  } catch (err) {
    alert('Erro ao exportar relatório.');
    console.error(err);
  }
}


